.tickets__controls-list {
  list-style: none;
  padding: 0;
  display: flex;
  max-width: 100%;
  overflow: auto;
}

.tickets__control-item {
  margin: 0;
  padding: 0 46px 0 0;


  &:last-child {
    padding-right: 0;
  }

  @include vp-1023 {
    padding-right: 54px;
  }

  @include vp-767 {
    padding-right: 45px;

  }
}

.tickets__control-button {
  @include font($ff-roboto, 400, 20px, rgba($color-sapphire, 0.5), 150%);
  background-color: $color-ghost-white;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;

  &:hover {
    color: $color-crimson;
  }

  @include vp-1023 {
    font-size: 18px;
  }

  @include vp-767 {
    font-size: 16px;
  }
}

.tickets__control-button.is-active  {
  padding-bottom: 10px;
  border-bottom: 2px solid $color-crimson;

  @include vp-1023 {
    padding-bottom: 12px;
  }

  @include vp-767 {
    padding-bottom:11px;
    border-bottom: 2px solid $color-crimson;
  }
}

.tickets__controls-wrapper {
  position: relative;
  margin: 0 0 36px;
  padding: 0;
  height: 59px;
  max-width: 100%;

  @include vp-1023 {
    margin-bottom: 43px;
    height: 59px;
  }

  @include vp-767 {
    margin-bottom: 33px;
    height: 53px;
  }
}

.tickets__controls-line {
  width: 100%;
  height: 2px;
  background-color: rgba($color-grey, 0.20);
  position: absolute;
  bottom: 1px;

  @include vp-1023 {
    bottom: 2px;
  }

  @include vp-767 {
    bottom: 0;
  }
}
